import axios from 'axios';
import { authorizationHeader } from '../helpers/authorizationHeader';
/**
 * User service
 */
var UserService = /** @class */ (function () {
    function UserService() {
    }
    /**
     * Changes password
     * @param oldPassword Old password
     * @param newPassword New password
     */
    UserService.prototype.changePassword = function (oldPassword, newPassword) {
        var body = {
            old: oldPassword,
            new: newPassword
        };
        return axios.put('user/password', body, { headers: authorizationHeader() });
    };
    /**
     * Adds the new user
     * @param username Username
     * @param password Password
     * @param language Language
     * @param role Role
     */
    UserService.prototype.add = function (username, password, language, role) {
        var body = {
            username: username,
            password: password,
            language: language,
            role: role,
        };
        return axios.post('users/', body, { headers: authorizationHeader() });
    };
    /**
     * Deletes the user
     * @param id User ID
     */
    UserService.prototype.delete = function (id) {
        return axios.delete('users/' + id, { headers: authorizationHeader() });
    };
    /**
     * Edits the user
     * @param id User ID
     * @param user User settings
     */
    UserService.prototype.edit = function (id, user) {
        return axios.put('users/' + id, user, { headers: authorizationHeader() });
    };
    /**
     * Returns the user
     * @param id User ID
     */
    UserService.prototype.get = function (id) {
        return axios.get('users/' + id, { headers: authorizationHeader() });
    };
    /**
     * Returns information about the logged in user
     */
    UserService.prototype.getLoggedIn = function () {
        return axios.get('user', { headers: authorizationHeader() });
    };
    /**
     * Lists all users
     */
    UserService.prototype.list = function () {
        return axios.get('users', { headers: authorizationHeader() });
    };
    return UserService;
}());
export default new UserService();
